export default {
  data() {
    return {
      user: { free_premium_items: 0 },
    }
  },
  methods: {
    async create() {
      await fetch(
        'https://f7b0efc1-99d2-4a67-a280-f2632dc7ed81.mock.pstmn.io/users',
        {
          method: 'POST',
          body: this.user,
        }
      )
        .then((res) => res.json())
        .then(({ status }) => {
          status == 'success' &&
            this.$router.push({
              path: '/',
            })
        })
    },
  },
}
